/* ==============================================================
   Module
   Box
   ==============================================================*/
/* MODULE BOX */
.module {
    &__box {
        margin-bottom: 15px;
        border: 2px solid @gwblue;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__box-title {
        display: flex;
        align-items: center;
        padding: 7px 15px;
        background-color: @gwblue;
        h3 {
            display: flex;
            flex-grow: 1;
            align-items: center;
            margin: 0;
            padding: 0;
            font-family: @osSemibold;
            font-size: 1.16667rem;
            color: @white;
            .fa {
                margin-right: 10px;
            }
            .btn {
                display: none;
                margin-left: auto;
                &:active, 
                &:focus, 
                &:active:focus {
                    color: @white;
                }
                .fa {
                    margin: 0;
                    transition: all 0.2s linear;
                }
                &.in {
                    .fa {
                        transform: rotate(-180deg);
                    }
                }
            }
        }
    }
    &__box-title-actions {
        margin-left: auto;
        .btn {
            color: @white;
            padding-left: 10px;
            &:active, 
            &:focus, 
            &:active:focus {
                color: @white;
            }
        }
        .dropdown-menu {
            padding: 0;
            border-radius: 0;
            border: 2px solid @gwblue;
            transform: translate3d(-129px, 18px, 0px) !important;
        }
    }
    &__box-body {
        max-height: calc(~"100vh - 110px");
        padding: 15px;
        background-color: @white;
        overflow-y: auto;
        hr {
            margin-right: -15px;
            margin-left: -15px;
        }
        p {
            &:last-child {
                margin: 0;
            }
        }
    }
    /* MODIFICATION */
    &__box--gray {
        border-color: @gray;
        .module__box-title {
            background-color: @gray;
        }
        .module__box-title-actions {
            .dropdown-menu {
                border-color: @gray; 
            }
        }
    }
    &__box--white {
        border-width: 1px;
        border-color: @bordergray;
        .module__box-title {
            background-color: @white;
            h3 {
                color: @black;
                .btn {
                    &:active, 
                    &:focus, 
                    &:active:focus {
                        color: @black;
                    }
                }
            }
        }
        .module__box-title-actions {
            .btn {
                color: @black;
                &:active, 
                &:focus, 
                &:active:focus {
                    color: @black;
                }
            }
            .module__box-title-actions {
                .dropdown-menu {
                    border-width: 1px;
                    border-color: @bordergray; 
                }
            }
        }
        .module__box-body {
            border-top: 1px solid @bordergray;
        }
    }
    &__box--collapsable {
        .module__box-title {
            h3 {
                cursor: pointer;
                .btn {
                    display: inline-block;
                }
            }
        }
    }
}